.btn-image{
  width: 236px;
  height: 33px;
 margin-top: 30px;
  position: relative;
  background: var(--background-color);
  border-radius: 10px;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 3px;
  padding: 2px;
  border: none;
  color: #000000;
  z-index: 9;
  --background-color: #FFFF00;
  --border-size:2px;
}
.btn-image::before{
    content: '';
    position: absolute;
    border-radius: 10px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  border: var(--border-size) solid var(--background-color);
  transition: top,left,right,bottom, 1000ms, ease-in-out;
}
.btn-image:hover::before,
.btn-image:focus::before{
  top: calc(var(--border-size) * -2);
  left: calc(var(--border-size) * -2);
  right: calc(var(--border-size) * -2);
  bottom: calc(var(--border-size) * -2);
}
  
